<template>
  <view class="main-container">
    <!-- 服务点单 -->
    <view class="left" @click="handleClick('fwdd')">
      <image class="img" src="https://cdn-static.papa.com.cn/social/home-bg-order.png" alt="" />
    </view>
    <view class="right">
      <!-- 运动课堂 -->
      <view class="rt-top" @click="handleClick('ykt')">
        <image class="img" src="https://cdn-static.papa.com.cn/social/home-bg-class.png" alt="" />
      </view>
      <!-- 运动地图 -->
      <view class="rt-bottom" @click="handleClick('ydtd')">
        <image class="img" src="https://cdn-static.papa.com.cn/social/home-bg-map.png" alt="" />
      </view>
    </view>
  </view>
</template>

<script>
export default {
  emits: ['click'],
  data() {
    return {
      urlMap: {
        fwdd: '/pagesSub/sportsService/sprotOrder/form',
        ydtd: '/pages/instructorSite/index',
        ykt: '/pagesSub/platform/yunLesson/index',
      },
    }
  },
  computed: {},
  onLoad(options) {},
  methods: {
    async handleClick(type) {
      const url = this.urlMap[type]
      if (!url) {
        uni.showToast({
          title: '敬请期待',
          icon: 'none',
        })
        return
      }
      if (type === 'fwdd') {
        let flag = await getApp().judgeIsAuth()
        if (!flag) {
          return
        }
      }
      uni.navigateTo({
        url,
      })
    },
  },
}
</script>

